.services {
  background-color: #333;
  color: #ccc;
  padding: 2rem 1rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  overflow-x: hidden;
}

.service-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  width: 100%;
}

.service-item {
  background-color: #444;
  margin: 0.5rem;
  padding: 1.5rem;
  flex: 1 1 calc(25% - 2rem);
  min-width: 220px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.service-item:hover {
  transform: translateY(-10px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.service-item h3 {
  margin-bottom: 1rem;
  color: #ffd700;
}

.service-item p {
  font-size: 1rem;
  line-height: 1.5;
}

.services h2 {
  font-size: 4rem;
  margin-bottom: 1rem;
  color: #ffd700;
}
