.about {
  background-color: #2c2c2c;
  color: #ddd;
  padding: 2rem 1rem; /* Уменьшаем отступы */
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; /* Центрируем содержимое */
  min-height: 50vh; /* Уменьшаем минимальную высоту */
  overflow-x: hidden; /* Убираем горизонтальную прокрутку */
}

.about-logo {
  height: 100px;
  margin-bottom: 1rem;
}

.about-photo {
  border-radius: 50%; /* Делаем фотографию круглой */
  width: 250px; /* Уменьшаем ширину */
  height: 250px; /* Уменьшаем высоту */
  object-fit: cover; /* Обрезаем изображение по центру */
  margin: 1rem 0; /* Добавляем отступы */
}

.about h2 {
  font-size: 4rem; /* Еще больше увеличиваем размер шрифта */
  margin-bottom: 1rem;
  color: #ffd700; /* золотой цвет заголовка */
}

.about p {
  font-size: 1.2rem; /* Увеличиваем размер шрифта */
  line-height: 1.6; /* Увеличиваем высоту строки */
  max-width: 600px;
  margin: 0 auto;
  font-weight: bold; /* Делаем шрифт жирным */
}
