body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #222;
  color: #fff;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden; /* Убираем горизонтальную прокрутку */
}

.App {
  display: flex;
  flex-direction: column;
  flex: 1;
  text-align: center;
  height: 100%;
}

main {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-x: hidden; /* Убираем горизонтальную прокрутку */
}

h2 {
  color: #eee;
}
