.contact {
  background-color: #2c2c2c;
  color: #bbb;
  padding: 1rem 1rem; /* Уменьшаем отступы */
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50vh; /* Уменьшаем минимальную высоту */
  overflow-x: hidden;
}

.contact h2 {
  font-size: 4rem;
  margin-bottom: 1rem;
  color: #ffd700; /* золотой цвет заголовка */
}

.contact-links p {
  font-size: 1.2rem;
  line-height: 1.6;
  margin: 0.5rem 0;
}

.contact a {
  color: #ffd700;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact a:hover {
  color: #fff;
}
